.scroll-content {
    padding-right: 50px; }
#my-scrollbar {
    height: 339px;
    overflow: auto; }
#my-scrollbar::-webkit-scrollbar {
  width: 9px; }
#my-scrollbar::-webkit-scrollbar-thumb {
  background: #4D434B;
  cursor: pointer;
  height: 10px;
  border-radius: 7px; }
#my-scrollbar::-webkit-scrollbar-track {
  background: #C4C4C4;
  border-radius: 7px; }
#my-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #4D434B; }
